<template>
  <div class="about">
    <h1>  </h1>
    <div>  .</div>
    <v-card v-if="openOsirisRegister">
      <v-card-text>
        <div class="display-1 text--primary">
          <p> </p>
        </div>
        <div>
          <p>-------[ AP :   ]-------</p>
        </div>
        <br>
        <br>
        <div class="display-1 text--primary">
          <p> </p>
        </div>
        <div>
          <p>-------[ GP :  (8/16) 9 ]-------</p>
          <p>-------[ Gp :  (8/16) 11 ]-------</p>
        </div>
        <br>
        <br>
        <div>
          <p style="color:red;">  (:)</p>
          <p style="color:red;"> !!</p>
        </div>
        <br>
        <v-container>
            <v-row>
                <v-flex xs12 sm6 md4>
                        <v-overflow-btn
                          class="my-2"
                          v-model="selectedMember"
                          :items="dropdownMember"
                          label=""
                          editable
                        >
                        </v-overflow-btn>
                      </v-flex>
            </v-row>
            <v-row>
                <v-flex xs12 sm6 md4>
                        <v-overflow-btn
                          class="my-2"
                          v-model="selectedGroup"
                          :items="dropdownGroup"
                          label=" "
                          editable
                        >
                        </v-overflow-btn>
                      </v-flex>
            </v-row>
          </v-container>
        </v-card-text>
      <v-card-actions>
        <v-btn color="success" dark class="mb-2" text @click="register"></v-btn>  
      </v-card-actions>
      <div v-if="isOver" class="red lighten-3 white--text">
       {{ msg }}
      </div>
      <div v-else class="green lighten-3 white--text">
       {{ msg }}
      </div>
    </v-card>
    <v-card v-else>
      <v-card-title>
          <span class="headlin">   .</span>
      </v-card-title>
    </v-card>
  </div>
</template>
<script>
export default {
  data: () => ({
    groups: [],
    members: [],
    selectedGroup: "",
    selectedMember: "",
    openOsirisRegister: false,
    msgColor: "",
    isOver: false,
    msg: ""
  }),
  created() {
    this.getInformation()
  },
  methods: {
    getInformation() {
      this.$http.get("api/osiris/group").then(response => {
        this.groups = response.data.data;
        console.log(response.data.data);
      });
      this.$http.get("api/member/").then(response => {
        this.members = response.data.data;
        console.log(response.data.data);
      });
      this.$http.get("api/config").then(response => {
        this.openOsirisRegister = response.data.data.openOsirisRegister;
        console.log(response.data.data);
      });
    },
    register() {
      if(this.dropdownMember.indexOf(this.selectedMember) == -1) {
        this.msg = `${this.selectedMember}   .  `
        return;
      }
      const data = { nickname: this.selectedMember, group: this.selectedGroup };
      this.$http.post("api/osiris/member", data).then(response => {
        if(response.data.msg === "OVER"){
          this.msg = ' .';
          this.isOver=true;
        } else if(response.data.msg === "NEW"){
          this.msg = `${this.selectedMember}  ${this.selectedGroup}  .`
          this.isOver=false;
        } else {
          this.msg = `${this.selectedMember}  ${this.selectedGroup} .`
          this.isOver=false;
        }
        console.log(response.data.data);
      }, error => {
        console.log(error);
      }
      );
    }
  },
  computed: {
    dropdownGroup() {
      return this.groups.map(group=>group.name);
    },
    dropdownMember() {
      return this.members.map(member=>member.nickname);
    }
  }
}
</script>
