<template>
  <v-container fluid >
    <p>     . </p>
    <v-switch 
      :label="`-   ${openOsiris?'':''}`"
      v-model="openOsiris"
      @change="changeOpenOsiris()">
    </v-switch>
    <v-switch 
      :label="`-   ${openOsirisRegister?'':''}`"
      v-model="openOsirisRegister" @change="changeOsirisRegister()"></v-switch>
    <v-btn color="error" dark class="mb-2" @click="deleteAll">  </v-btn>
    <v-dialog v-model="dialog" max-width="500px">
      <template v-slot:activator="{ on }">
        <v-btn color="success" dark class="mb-2" v-on="on">   </v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="headlin">   </span>
        </v-card-title>
        <v-card-text>
          <strong style="color:orangered;">*</strong> : 
          <v-contianer>
            <v-row>
              <v-col xs12 sm6 md4> 
                <v-text-field v-model="editedItem.name">
                  <template v-slot:label>
                     <strong style="color:orangered;">*</strong>
                  </template>
                </v-text-field>
              </v-col>
              <v-col xs12 sm6 md4> 
                <v-text-field v-model="editedItem.time">
                  <template v-slot:label>
                      <strong style="color:orangered;">*</strong>
                  </template>
                </v-text-field>
              </v-col>
            </v-row>
          </v-contianer>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
            <v-btn color="error" @click="close">Cancel</v-btn>
            <v-btn color="primary" @click="save">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-container fluid>
    <v-data-table 
                   dense 
                   :headers="headers"
                   :items="groups"
                   disable-pagination
                   :hide-default-footer="true"
                   class="elevation-1">
      <template slot="items" slot-scope="props">
        <td>{{ props.item.name }} </td>
        <td>{{ props.item.time }} </td>
        <td>
          <v-icon medium class="mr-2" @click="deleteItem(props.item)">delete</v-icon>
        </td>
      </template>
    </v-data-table>
    </v-container>
  </v-container>
